import { defineComponent, ref, onMounted, computed } from 'vue';
import SidebarComponent from '../components/SidebarComponent.vue';
import ButtonComponent from '../components/ButtonComponent.vue';
import AddUserForm from '../components/AddUserForm.vue';
import NavBar from '../components/NavBar.vue';
import { useRouter } from 'vue-router';
import { listUsers } from '../api/authService';
export default defineComponent({
    name: "UnifiedListWithAccessControl",
    components: {
        SidebarComponent,
        ButtonComponent,
        AddUserForm,
        NavBar
    },
    props: {
        entityType: {
            type: String,
            required: true,
        },
        currentRole: {
            type: String,
            required: true,
            validator: (value) => ['Admin', 'Client', 'Manager'].includes(value),
        },
    },
    setup(props) {
        const entities = ref([]);
        const entityTitle = ref("");
        const showAddUserForm = ref(false);
        const role = ref(localStorage.getItem("role") || "User");
        const menuItems = ref([
            { name: 'Tableau de Bord', link: '/dashboard', active: true, visibleTo: ['Admin', 'Client', 'Manager'] },
            { name: 'Gestion Utilisateur', link: '/liste', active: false, visibleTo: ['Admin', 'Client', 'Manager'] },
            { name: "Profil d'utilisateur", link: '/profile', active: false, visibleTo: ['Admin', 'Client', 'Manager'] },
        ]);
        const router = useRouter();
        const filteredMenuItems = computed(() => {
            return menuItems.value.filter(item => item.visibleTo.includes(role.value));
        });
        const canAddEntity = computed(() => {
            return (props.currentRole === 'Admin' ||
                (props.currentRole === 'Client' && props.entityType !== 'Client') ||
                (props.currentRole === 'Manager' && props.entityType === 'Utilisateur'));
        });
        const canEditOrDelete = computed(() => props.currentRole !== 'Utilisateur');
        const loadEntities = async () => {
            try {
                let response;
                const entityType = role.value;
                switch (entityType) {
                    case "Admin":
                        entityTitle.value = "Admin";
                        break;
                    case "Client":
                        entityTitle.value = "Client";
                        break;
                    case "Manager":
                        entityTitle.value = "Manager";
                        break;
                    case "Utilisateur":
                        entityTitle.value = "Utilisateur";
                        break;
                    default:
                        console.warn(`Type d'entité inconnu: ${entityType}`);
                        return;
                }
                // Only call listUsers once, since it is used in every case
                response = await listUsers(entityTitle.value);
                if (response && Array.isArray(response)) {
                    entities.value = response;
                }
                else {
                    console.warn("Aucune donnée reçue pour la liste des entités.");
                }
            }
            catch (error) {
                console.error("Une erreur est survenue lors de la récupération des données.", error);
            }
        };
        onMounted(loadEntities);
        const editEntity = (entityId) => {
            if (canEditOrDelete.value) {
                console.log("Edit entity:", entityId);
            }
        };
        const deleteEntity = (entityId) => {
            if (canEditOrDelete.value) {
                console.log("Delete entity with ID:", entityId);
            }
        };
        const toggleAddUserForm = () => {
            if (canAddEntity.value) {
                showAddUserForm.value = !showAddUserForm.value;
            }
        };
        const refreshList = () => {
            showAddUserForm.value = false;
            loadEntities();
        };
        return {
            entities,
            entityTitle,
            filteredMenuItems,
            showAddUserForm,
            toggleAddUserForm,
            canAddEntity,
            canEditOrDelete,
            editEntity,
            deleteEntity,
            refreshList,
        };
    },
});
